}

body {
  background-color: var(--primary-cl);
  color: var(--dark-cl);
  font-family: Neue Montreal, sans-serif;
  font-style: normal;
  font-weight: 400;
}

.container {
  min-width: 320px;
  max-width: 375px;
  margin: 0 auto;
  padding: 0 20px;
}

